import "./App.css";
import Header from "./components/header";
import Home from "./components/home";
import ScrollToTop from "./components/scrolltotop";
import { Routes, Route } from "react-router-dom";
import { useState } from "react";
import Bcmainmenu from "./components/bcmenu";
import Pcmainmenu from "./components/pcmenu";
import Ccmainmenu from "./components/ccmenu";
import SimpleBackdrop from "./components/backdrop";
import Contact from "./components/contact";
import $ from "jquery";
import Footer from "./components/footer";
import Hto from "./components/howtoorder";

function App() {
  const [itemCount, setitemCount] = useState(0);

  const [backdrop, setbackdrop] = useState(true);

  $(window).on("load", () => {
    setbackdrop(false);
  });

  return (
    <div>
      <Header itemCount={itemCount} />
      <ScrollToTop />

      {backdrop && <SimpleBackdrop />}
      {!backdrop && (
        <>
          <Routes>
            <Route
              path="/*"
              element={
                <Home setitemCount={setitemCount} itemCount={itemCount} />
              }
              exact
            />

            <Route path="/HowToOrder" element={<Hto />} />
            <Route path="/ContactUs" element={<Contact />} />
            <Route path="/BakeryCakes" element={<Bcmainmenu />} />
            <Route path="/PremiumCakes" element={<Pcmainmenu />} />
            <Route path="/CustomizedCakes" element={<Ccmainmenu />} />
          </Routes>

          <Footer />
        </>
      )}
    </div>
  );
}

export default App;
