import Carousal from "./carousal";
import Menu from "./menu";
import Whatsapp from "./whatsapp";
import Helmet from "react-helmet";

function Home({ setitemCount, itemCount }) {
  return (
    <div> <Helmet>
    <title>
    yoCAKE | Home
    </title>
  </Helmet>
      <Carousal />
      <Menu setitemCount={setitemCount} itemCount={itemCount} />
      <Whatsapp/>
    </div>
  );
}
export default Home;
