import React from "react";
import Helmet from "react-helmet"


export default function contact() {
  return (
    <>
       <Helmet>
        <title>
        yoCAKE | Contact Us
        </title>
      </Helmet>
    <div className="container " style={{ maxWidth: "640px" }}>
      <section id="getintouch" className="p-3 bg-light mb-1">
        <div className="row  justify-content-center row-cols-1 ">
          <div
            className="col p-3  my-4"
            style={{ color: " #FFBD59", backgroundColor: "#100600" }}
          >
            <div className="mb-3">
              <h3>Reach Us</h3>
            </div>
            <div className="my-2 d-flex  align-items-center">
              <div className="fw-bold ">Email-Id :</div>
              <div className="ms-4 fw-bold ">yocake.pune@gmail.com</div>
            </div>
            <div className="my-2 d-flex  align-items-center">
              <div className="fw-bold ">Contact Number :</div>
              <div className="ms-4 fw-bold ">+91 92091 03743</div>
            </div>
          </div>
          <div className="col p-3  ">
            <div className="mb-4">
              <h3>Get In Touch With Us</h3>
            </div>

            <div className="">
              <form
                id="my-form"
                action="https://formspree.io/f/xnqwzzjk"
                method="POST"
              >
                <div className="mb-3">
                  <label htmlFor="" className="form-label">
                    Your Name
                  </label>

                  <input
                    type="text"
                    id="name"
                    placeholder="Enter your name"
                    className="form-control capi p-2"
                    name="name"
                    required
                  />
                </div>

                <div className="mb-3">
                  <label htmlFor="" className="form-label">
                    Email Id
                  </label>
                  <input
                    type="email"
                    placeholder="Enter your email-id"
                    className="form-control capi p-2"
                    id="email"
                    name="email"
                    required
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="" className="form-label">
                    Contact Number
                  </label>
                  <input
                    type="text"
                    placeholder="Enter your contact number"
                    className="form-control capi p-2"
                    id="phone"
                    name="phone"
                    required
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="" className="form-label">
                    How can we help you?
                  </label>
                  <textarea
                    className="form-control"
                    id="message"
                    placeholder="Enter your message"
                    rows="2"
                    name="message"
                    required
                  ></textarea>
                </div>

                <div className=" text-center py-2 ">
                  <button
                    className="btn fw-bold fs-6 btn-sm p-2"
                    type="submit"
                    
                    style={{
                      backgroundColor: " #FFBD59",
                      color: "#100600",
                    }}
                  >
                    SUBMIT
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
    </div>
    </>
  );
}
